import { useState } from 'react';
import './App.css';


function App() {
  let [num, setNum] = useState(0)

  const increment = () => {
    setNum(num + 10)
  }
  const decrement = () => {
    setNum(num - 10)
  }

  /*JSX*/
  return (
    <div id="outer">
      <div id="main__container">
        <h1 id="main__text">{num}</h1>
        <div className="button__collection">
          <h1>.</h1>
          <button onClick={increment}>ADD</button>
          <button onClick={decrement}>SUB</button>
        </div>
      </div>
    </div>
  );
}
/*onClick()*/
export default App;
